import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import SiteLayout from './SiteLayout';
import { contentService } from '../../services/contentService';
import './SitePage.css';

//   
import Home from '../static/Home';
import About from '../static/About';
import Departments from '../static/Departments';
import Contacts from '../static/Contacts';
import NewsList from '../news/NewsList';
import NewsItem from '../news/NewsItem';
import GalleryPage from './gallery/GalleryPage';

//    
import TransparencySection from './TransparencySection';

const SitePage = () => {
  const { pageName } = useParams();
  const [content, setContent] = useState('');
  const [loading, setLoading] = useState(false);

  //   ,  ,      contentService
  const dynamicPages = ['transparency']; //  ,  transparency   

  useEffect(() => {
    //    transparency     contentService
    if (pageName && pageName !== 'transparency') {
      loadContent();
    }
  }, [pageName]);

  const loadContent = async () => {
    setLoading(true);
    try {
      const response = await contentService.getContent(pageName);
      setContent(response.data.content || '');
    } catch (error) {
      console.error('   :', error);
      setContent('<p>  </p>');
    } finally {
      setLoading(false);
    }
  };

  //  
  const renderContent = () => {
    //      
    if (pageName === 'transparency') {
      return <TransparencySection />;
    }

    //     
    if (loading) {
      return <div style={{ padding: '2rem', textAlign: 'center' }}>...</div>;
    }

    switch (pageName) {
      case 'home':
        return <Home />;
      case 'about':
        return <About />;
      case 'departments':
        return <Departments />;
      case 'contacts':
        return <Contacts />;
      case 'news':
        return <NewsList />;
      case 'gallery':
        return <GalleryPage />;
      default:
        return (
          <div dangerouslySetInnerHTML={{ __html: content }} />
        );
    }
  };

  return (
    <SiteLayout>
      {renderContent()}
    </SiteLayout>
  );
};

//     
export const NewsPage = () => {
  const { id } = useParams();

  if (id) {
    return (
      <SiteLayout>
        <NewsItem />
      </SiteLayout>
    );
  }

  return (
    <SiteLayout>
      <NewsList />
    </SiteLayout>
  );
};

export default SitePage;
