import React, { useState, useEffect } from 'react';
import galleryService from '../../../services/galleryService';
import './GalleryPage.css';

const GalleryPage = () => {
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadGalleryImages();
  }, []);

  const loadGalleryImages = async () => {
    try {
      const response = await galleryService.getGalleryImages();
      setImages(response.images || []);
    } catch (error) {
      console.error('  :', error);
      //    
      setImages([
        { id: 1, src: '/img/gallery1.jpg', alt: ' ', title: '   ' },
        { id: 2, src: '/img/gallery2.jpg', alt: ' ', title: ' ' },
        { id: 3, src: '/img/gallery3.jpg', alt: ' ', title: ' ' },
        { id: 4, src: '/img/gallery4.jpg', alt: ' ', title: ' ' },
      ]);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="gallery-page">
        <div className="container">
          <div className="loading"> ...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="gallery-page">
      <div className="container">
        <p>    </p>
        
        <div className="gallery-grid">
          {images.map((image) => (
            <div key={image.id} className="gallery-item">
              <img 
  src={image.image_url} 
  alt={image.title || image.alt || ''} 
  loading="lazy"
  onError={(e) => (e.target.src = '/img/placeholder.jpg')}
/>
              <div className="image-overlay">
                <span className="image-title">{image.title || image.alt}</span>
              </div>
            </div>
          ))}
        </div>

        {images.length === 0 && (
          <div className="empty-gallery">
            <p>    .  ,  .</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default GalleryPage;