import React, { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

const SiteLayout = ({ children }) => {
  const location = useLocation();
  const isHomePage = location.pathname === '/site/home';

  useEffect(() => {
    const initializeScripts = () => {
      // Dropdown 
      const dropdowns = document.querySelectorAll('.dropdown');
      dropdowns.forEach(dropdown => {
        dropdown.addEventListener('mouseenter', () => {
          dropdown.querySelector('.dropdown-content').style.display = 'block';
        });
        dropdown.addEventListener('mouseleave', () => {
          dropdown.querySelector('.dropdown-content').style.display = 'none';
        });
      });

      //  
      const modals = document.querySelectorAll('.modal');
      modals.forEach(modal => {
        modal.addEventListener('click', (e) => {
          if (e.target === modal) {
            modal.style.display = 'none';
          }
        });
      });

      const closeButtons = document.querySelectorAll('.modal .close');
      closeButtons.forEach(button => {
        button.addEventListener('click', () => {
          button.closest('.modal').style.display = 'none';
        });
      });
    };

    initializeScripts();

    return () => {
      // Cleanup
      const dropdowns = document.querySelectorAll('.dropdown');
      dropdowns.forEach(dropdown => {
        dropdown.removeEventListener('mouseenter', () => {});
        dropdown.removeEventListener('mouseleave', () => {});
      });
    };
  }, [location]);

  return (
    <div className="antares-site">
      <header>
        {isHomePage ? (
          //       
          <div className="header-image">
            <img src="/img/baner.jpg" alt="  " />
            <nav className="main-nav">
              <div className="nav-left">
                <Link to="/site/departments"></Link>
                <Link to="/site/about"> </Link>
                <Link to="/site/gallery">/</Link>
                <Link to="/site/news"></Link>
              </div>
              
              <div className="nav-center">
                <Link to="/site/home">
                  <img src="/img/logo_nav.png" alt=" " className="logo" />
                </Link>
              </div>
              
              <div className="nav-right">
                <Link to="/site/transparency"></Link>
                {/*  "" */}
                <Link to="/site/contacts"></Link>
              </div>
            </nav>
            
            <h1>    </h1>
            
            <div className="promo-banner">
              <span className="promo-text">  </span>
              <a 
                href="https://forms.gle/_" 
                target="_blank" 
                rel="noopener noreferrer" 
                className="promo-button"
              >
                
              </a>
            </div>
          </div>
        ) : (
          //        
          <div className="simple-header">
            <nav className="main-nav">
              <div className="nav-left">
                <Link to="/site/departments"></Link>
                <Link to="/site/about"> </Link>
                <Link to="/site/gallery">/</Link>
                <Link to="/site/news"></Link>
              </div>
              
              <div className="nav-center">
                <Link to="/site/home">
                  <img src="/img/logo_nav.png" alt=" " className="logo" />
                </Link>
              </div>
              
              <div className="nav-right">
                <Link to="/site/transparency"></Link>
                {/*  "" */}
                <Link to="/site/contacts"></Link>
              </div>
            </nav>
          </div>
        )}
      </header>

      <main className={isHomePage ? '' : 'simple-main'}>
        {children}
      </main>

      <footer>
        <div className="container">
           {new Date().getFullYear()}     .   .
        </div>
      </footer>

      {/*   */}
      <div id="signupModal" className="modal">
        <div className="modal-content">
          <span className="close">&times;</span>
          <h2>  </h2>
          <form>
            <input type="text" placeholder=" '" required />
            <input type="tel" placeholder="" required />
            <input type="email" placeholder="Email" required />
            <button type="submit"></button>
          </form>
        </div>
      </div>

      <div id="tuitionModal" className="modal">
        <div className="modal-content">
          <span className="close">&times;</span>
          <h2>  </h2>
          <p>   ...</p>
        </div>
      </div>

      <div id="statuteModal" className="modal">
        <div className="modal-content">
          <span className="close">&times;</span>
          <h2> </h2>
          <p>   ""...</p>
        </div>
      </div>

      <div id="attestationModal" className="modal">
        <div className="modal-content">
          <span className="close">&times;</span>
          <h2>  </h2>
          <p>  ...</p>
        </div>
      </div>
    </div>
  );
};

export default SiteLayout;